@import url(https://fonts.googleapis.com/css2?family=Mukta&display=swap);
* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: "Mukta", sans-serif;
}

.main {
  background-image: linear-gradient(to bottom, rgb(184, 179, 179), white),
    url(https://t3.ftcdn.net/jpg/01/88/12/76/360_F_188127684_wljoa6x4UlRNw6qKv6TRId2d2juu66Mt.jpg);
  height: 100%;
}

.services .row > * {
  padding-right: 0;
  padding-left: 0;
}

.box {
  border: 1px solid grey;
  border-radius: 4px;
}

.offer-color {
  background-color: rgba(197, 188, 204, 0.653);
}

.footer p {
  margin-bottom: 0;
}

.logo {
  width: 80px;
}

.menubar {
  padding-top: 0rem;
  padding-bottom: 0rem;
}

.navbar-brand {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-style: italic;
  padding-top: 0rem;
  padding-bottom: 0rem;
}

